<template>
	<div class="saleedi-report-form">
		<van-tabs v-model="active" color="#4c96f3" class="report-form-tab">
			<van-tab title="按单据统计"></van-tab>
			<van-tab title="按货品统计"></van-tab>
		</van-tabs>
		<van-cell-group class="order-search-filter">
			<div class="date-cell">
				<van-field v-model="beginDate" readonly label="查询日期" placeholder="起始时间" @click="beginDateClick"/>
				<van-field v-model="enDate" readonly label="至" placeholder="结束时间" @click="endDateClick"/>
			</div>
			<van-field v-model="orderEnt" readonly label="订货企业" placeholder="请选择订货企业" @click="companyListShow = true" />
			<van-field v-model="orderStateText" readonly label="订单状态" placeholder="请选择订单状态" @click="stateListShow = true">
				<van-button slot="button" size="small" type="primary" @click="searchHandler">搜索</van-button>
			</van-field>
		</van-cell-group>
		<div class="report-form-warp">
			<w-table
				:tableData="reportOrderData"
				class="report-table"
				show-summary
				:summary-method="getSum"
				v-show="active === 0"
				@rowClick="rowClickHandle">
				<w-table-column prop="companyName" label="订货企业" align="left"></w-table-column>
				<w-table-column prop="orderBillAmount" label="订单数量"></w-table-column>
				<w-table-column prop="orderSumMoney" label="订货总金额" align="right"></w-table-column>
				<w-table-column prop="deliverySumMoney" label="发货总金额" align="right"></w-table-column>
			</w-table>
			<w-table
				:tableData="reportGoodsData"
				class="report-table"
				show-summary
				:summary-method="getSumGoodsReport"
				v-show="active === 1">
				<w-table-column prop="goodsName" label="品项名称" align="left"></w-table-column>
				<w-table-column prop="unitName" label="品项单位"></w-table-column>
				<w-table-column prop="orderAmount" label="订货总数量" align="right"></w-table-column>
				<w-table-column prop="deliverySumMoney" label="发货总金额" align="right"></w-table-column>
			</w-table>
		</div>
		<van-popup v-model="datePickerShow" position="bottom">
			<van-datetime-picker v-model="currentDate" type="date" :formatter="dataPickerFormatter" @confirm="dataConfirmhandle"/>
		</van-popup>
		<van-actionsheet
			v-model="companyListShow"
			:actions="companyList"
			cancel-text="取消"
			@select="onCompanySelect"
		/>
		<van-actionsheet
			v-model="stateListShow"
			:actions="stateList"
			cancel-text="取消"
			@select="onStateSelect"
		/>
	</div>
</template>

<script>
const { reportSumAddUnitFunc } = require('@/js/lib/common')
export default {
	name: 'orderList',
	data () {
		return {
			active: 0,
			beginDate: '',
			enDate: '',
			dateType: '',
			orderEnt: '',
			orderState: '',
			orderStateText: '',
			currentDate: new Date(),
			datePickerShow: false,
			reportOrderData: [],
			reportGoodsData: [],
			companyList: [],
			companyListShow: false,
			stateList: [
				{ name: '全部', value: '' },
				{ name: '待确认', value: '1' },
				{ name: '待收货', value: '2' },
				{ name: '已完成', value: '3' }
			],
			stateListShow: false
		}
	},
	created () {
		this.getCompany()
	},
	methods: {
		dataPickerFormatter (type, value) {
			if (type === 'year') {
				return `${value}年`
			} else if (type === 'month') {
				return `${value}月`
			}
			return `${value}日`
		},
		beginDateClick () {
			this.dateType = 'begin'
			this.datePickerShow = true
		},
		endDateClick () {
			this.dateType = 'end'
			this.datePickerShow = true
		},
		dataConfirmhandle (date) {
			if (this.dateType === 'begin') {
				this.beginDate = this.$fxUtils.setDate(date)
			} else {
				this.enDate = this.$fxUtils.setDate(date)
			}
			this.datePickerShow = false
		},
		getSum (param) {
			return reportSumAddUnitFunc(param, ['orderSumMoney', 'deliverySumMoney'])
		},
		getSumGoodsReport (param) {
			return reportSumAddUnitFunc(param, ['deliverySumMoney'])
		},
		onCompanySelect (item) {
			this.orderEnt = item.name
			this.companyListShow = false
		},
		onStateSelect (item) {
			this.orderState = item.value
			this.orderStateText = item.name
			this.stateListShow = false
		},
		getCompany () {
			this.$fxHttp.requestPost({
				url: '/getOrderCompany'
			}).then(res => {
				this.companyList = res
			})
		},
		getReportOrderData (params) {
			this.$fxHttp.requestPost({
				url: '/sum4BillByMap',
				data: params
			}).then(res => {
				this.reportOrderData = res
			})
		},
		getReportGoodsData (params) {
			this.$fxHttp.requestPost({
				url: '/sum4GoodsByMap',
				data: params
			}).then(res => {
				this.reportGoodsData = res
			})
		},
		searchHandler () {
			const params = {
				companyName: this.orderEnt,
				beginDate: `${this.beginDate} 00:00:01`,
				endDate: `${this.enDate} 23:59:59`,
				jump: '1'
			}
			if (this.orderState) {
				params.billState = this.orderState
			}
			if (this.active === 0) {
				this.getReportOrderData(params)
			} else {
				this.getReportGoodsData(params)
			}
		},
		rowClickHandle ([item, index]) {
			this.$router.push({
				path: '/reportFormEnt',
				query: {
					companyName: item.companyName,
					beginDate: `${this.beginDate} 00:00:01`,
					endDate: `${this.enDate} 23:59:59`
				}
			})
		}
	}
}
</script>
<style lang="stylus" scoped>
@import '../../assets/stylus/varsty.styl'
.saleedi-report-form {
	height:100%
	background:#fff
	display: flex
	flex-direction: column
	.order-search-filter {
		text-align:left
		.date-cell {
			width:100%
			display: flex
			flex-direction: row
		}
	}
	.report-form-warp {
		flex:1
		overflow:scroll
		.report-table {
			height:100%
		}
	}
}
</style>
